	module.exports = {"data":{"allPostTitles":{"edges":[{"node":{"frontmatter":{"title":"Aggregates","lesson":2,"category":"akkatecture","chapter":2,"type":"docs"},"fields":{"slug":"/aggregates"}}},{"node":{"frontmatter":{"title":"Akka","lesson":5,"category":"akkatecture","chapter":2,"type":"docs"},"fields":{"slug":"/akka"}}},{"node":{"frontmatter":{"title":"Commands","lesson":4,"category":"akkatecture","chapter":2,"type":"docs"},"fields":{"slug":"/commands"}}},{"node":{"frontmatter":{"title":"Events","lesson":3,"category":"akkatecture","chapter":2,"type":"docs"},"fields":{"slug":"/events"}}},{"node":{"frontmatter":{"title":"Subscribers","lesson":6,"category":"akkatecture","chapter":2,"type":"docs"},"fields":{"slug":"/subscribers"}}},{"node":{"frontmatter":{"title":"Configuration","lesson":7,"category":"akkatecture","chapter":2,"type":"docs"},"fields":{"slug":"/configuration"}}},{"node":{"frontmatter":{"title":"Primitives","lesson":1,"category":"akkatecture","chapter":2,"type":"docs"},"fields":{"slug":"/primitives"}}},{"node":{"frontmatter":{"title":"Snapshotting","lesson":2,"category":"akkatecture","chapter":4,"type":"docs"},"fields":{"slug":"/snapshotting"}}},{"node":{"frontmatter":{"title":"Clustering","lesson":2,"category":"akkatecture","chapter":4,"type":"docs"},"fields":{"slug":"/clustering"}}},{"node":{"frontmatter":{"title":"Sagas","lesson":1,"category":"akkatecture","chapter":4,"type":"docs"},"fields":{"slug":"/sagas"}}},{"node":{"frontmatter":{"title":"Production Readiness","lesson":4,"category":"akkatecture","chapter":4,"type":"docs"},"fields":{"slug":"/production-readiness"}}},{"node":{"frontmatter":{"title":"Articles","lesson":2,"category":"akkatecture","chapter":5,"type":"docs"},"fields":{"slug":"/articles"}}},{"node":{"frontmatter":{"title":"Tips and Tricks","lesson":1,"category":"akkatecture","chapter":5,"type":"docs"},"fields":{"slug":"/tips-and-tricks"}}},{"node":{"frontmatter":{"title":"Getting Started","lesson":1,"category":"akkatecture","chapter":1,"type":"docs"},"fields":{"slug":"/getting-started"}}},{"node":{"frontmatter":{"title":"Your First Aggregate","lesson":1,"category":"akkatecture","chapter":3,"type":"docs"},"fields":{"slug":"/your-first-aggregate"}}},{"node":{"frontmatter":{"title":"Your First Aggregate Saga","lesson":4,"category":"akkatecture","chapter":3,"type":"docs"},"fields":{"slug":"/your-first-aggregate-saga"}}},{"node":{"frontmatter":{"title":"Your First Read Model","lesson":3,"category":"akkatecture","chapter":3,"type":"docs"},"fields":{"slug":"/your-first-read-model"}}},{"node":{"frontmatter":{"title":"Your First Subscriber","lesson":2,"category":"akkatecture","chapter":3,"type":"docs"},"fields":{"slug":"/your-first-subscriber"}}},{"node":{"frontmatter":{"title":"Videos","lesson":3,"category":"akkatecture","chapter":5,"type":"docs"},"fields":{"slug":"/videos"}}}]},"postBySlug":{"html":"<p>Akkatecture is set of constructs and patterns written in C# ontop of akka.net. The main goal of Akkatecture is to allow developers who are using akka.net to model their business domain within the akka.net framework easily. Akkatecture is built on messaging &#x26; event based mode of operation, making it highly reactive and scalable. Akkatecture focusses on messaging and domain driven design integration patterns between domain entities within an akka actor system to make developing distributed domain driven design applications easier.</p>\n<p>Akkatecture uses the actor model as the universal primitive of concurrent computation. This means that aggregates, sagas, and other domain concepts are modelled within the actor model. Invoking or interacting with the domain is done by having these actors either react through commands (sent from outside of the aggregate boundary), or through domain events, emitted from within the domain boundary, both of which are fundamentally messages. A strong case can be made for using the actor system as a basis for designing your distributed domain, because actors in actor systems embody 3 things fundamentally:</p>\n<ol>\n<li><strong>Processing</strong> - actors can do work when requested to, the requested work can be initiated by a message, typically in the form of a command or an event, the locality of this processing is done within the instantiation of an actor itself.</li>\n<li><strong>Storage</strong> - actors can store local internal state, in memory, and defer storing its state to persistence. This state is also thread safe from anything outside of the actors locality since actors process one message at a time and cannot be inspected by normal means.</li>\n<li><strong>Communication</strong> - actors can communicate with each other using a pre-defined communication protocol. Typically addressed by using location transparent actor addresses. Actors communicate with message passing and that is the only way that the outside world can communicate with the actor system. Messages beign commands, or events.</li>\n</ol>\n<p>The actor model in computer science is a mathematical model of concurrent computation that treats \"actors\" as the universal primitives of concurrent computation. In response to a message that it receives, an actor can: make local decisions, create more actors, send more messages, and determine how to respond to the next message received. Actors may modify their own private state, but can only affect each other through messages (avoiding the need for any defining of critical sections, or wierd mutexs).</p>\n<p>Akka based systems have been used to drive hugely scalable and highly available systems (such as Netflix and The Guardian). Even though these companies tend to run into issues at scale on a daily basis, I still see value in modelling the business domain using actors since (with some haggling), they are quite descriptive of what actually happens in the real world.</p>\n<p>Please have a look at our documentation, go through the basic concepts, and the walkthroughs to get a good understanding of what akkatecture looks like. Akkatecture is intended for developers who understand cqrs / event sourcing. Knowledge of akka.net will give you more knowledge on how to extend Akkatecture through akka's highly extensible configuration.</p>\n<h3 id=\"status-of-akkatecture\"><a href=\"#status-of-akkatecture\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Status of Akkatecture</h3>\n<p>Akkatecture is still currently in development, however most of the basic building blocks have been built out, but as of yet no v1 to speak of on NuGet.</p>\n<p>The next plan is to work on the current core library and branch it out to support clustered deployment scenarios.</p>","timeToRead":2,"excerpt":"Akkatecture is set of constructs and patterns written in C# ontop of akka.net. The main goal of Akkatecture is to allow developers who are…","frontmatter":{"title":"Getting Started","cover":"https://unsplash.it/400/300/?random?BoldMage","date":"01/07/2018","category":"akkatecture","tags":["getting-started","akkatecture","csharp","dotnet"]},"fields":{"slug":"/getting-started"}}},"pathContext":{"slug":"/getting-started","category":"akkatecture"}}

